import Validator from 'validatorjs';
import {Toast} from "vant";


/**
 * @present  
 * @required 
 * @required_if:anotherfield,value anotherfield == value
 * @required_unless:anotherfield,value (anotherfield == value)
 * @required_with:foo,bar,... (foo,bar,...)
 * @required_with_all:foo,bar,... (foo,bar,...)
 * @required_without:foo,bar,... 
 * @required_without_all:foo,bar,... 
 * @accepted yeson1true
 * @in:foo,bar,...
 * @not_in:foo,bar,...
 * @date Date
 * @after:date
 * @after_or_equal:date
 * @before:date
 * @before_or_equal:date
 * @alpha 
 * @alpha_dash 
 * @alpha_num 
 * @digits:value value
 * @digits_between:min,max  min~max
 * @integer
 * @numeric
 * @string
 * @url
 * @array
 * @boolean truefalse01truefalse01
 * @email
 * @hex
 * @regex:pattern
 * @max:value
 * @min:value
 * @size:value [:()],[:]
 * @between:min,max
 * @confirmed  *_confirmation(@same)
 * @same:attribute (@confirmed)
 * @different:attribute 
 *
 * : https://github.com/skaterdav85/validatorjs
 *
 * @param data 
 * @param rules 
 * @param attributeNames 
 * @param customErrorMessages 
 */
export default function validator(
	data,
	rules,
	attributeNames = null,
	customErrorMessages = {}
) {
	Validator.useLang('zh')
	let validator = new Validator(data, rules, customErrorMessages);
	if (attributeNames && typeof attributeNames === 'object' && !Array.isArray(attributeNames)) {
		let names = {}
		for (let key in attributeNames) {
			names[key] = attributeNames[key]
		}
		validator.setAttributeNames(names);
	}

	if (validator.fails()) {
		let errors = Object.values(validator.errors.errors);
		let firstErrorMessage = '';
		for (let msgArr of errors) {
			firstErrorMessage = msgArr.shift();
			break;
		}
		Toast(firstErrorMessage)
		return false;

	}
	return true;
}
